import { Component, OnInit, OnChanges } from '@angular/core';
import { BigCusHttpService } from '../../http/big-cus.http.service';
import { Message, ConfirmationService } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { DecimalPipe } from '@angular/common';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { custNoTen, checkNumber, decimalNumTwo } from 'app/@uisftech/common/service/validator';
import { BigCusApproveBean } from './bean/bigCusApprove.bean';
@Component({
    selector: 'bigCustomerApprove',
    templateUrl: './bigCustomerApprove.component.html',
    styleUrls: ['./bigCustomerApprove.component.scss'],
    providers: [BigCusHttpService, DecimalPipe, ConfirmationService]
})
export class BigCustomerApproveComponent implements OnInit, OnChanges {
    msgs: Message[] = [];
    depositBase: string;//存款基期
    loanBase: string;//贷款基期

    detailList: any = [];
    arrayList: any = [];
    bigCustNo: string;
    dataMap: any;
    isShow: boolean = false;
    flag: string;
    isHistory: boolean = false;

    show: number = 2;
    detailItems = {
        custNo: '',
        custName: '',
        snrjckSum: '',
        snrjdkjckSum: '',
        feeunitPrice: '',
        depositBase: '',
        loanBase: '',
    }
    data_cuslist: any = [];//客户名单信息
    pageSizeCusList: number = 100;
    pageNumCusList: number = 1;
    totalCusList: number;

    dataTable: any = [];//大客户分成信息
    pageSizeDivide: number = 100;
    pageNumDivide: number = 1;
    totalDivide: number;

    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';

    userform: FormGroup;
    moneyform: FormGroup;
    custNo: string;
    custName: string;
    feeunitPrice: string;//费用单价
    saveFeeMon: string;//暂存的费用单价
    partyId: string;//工号
    lastName: string;//工号名称
    divideRate: string;//分成比例
    relDepOrgList: string;//
    custId: string;//工号
    custIdName: string;//姓名
    bigId: string;
    manageId: string;
    stop_flag: boolean = true;//调整进来不可以操作
    firstCodeFlag: boolean = false;//是否是首节点可以修改
    bigCusValue: any;
    bigCusApproveBean: BigCusApproveBean = new BigCusApproveBean();
    code: any;
    depositSumTypeOptions: any = [];//存款累计方式
    on_cust: string;//暂存的客户号
    on_userID: string;//暂存的协储员号

    //当前名单信息新增删除，数据变化之后重新刷新数据时用的标志
    custListFlag: number = 0;
    addFlagBtn: boolean = false;//名单信息添加开关
    addMoeyFlag: boolean = false;//费用信息添加开关
    constructor(
        public httpService: BigCusHttpService,
        public routeInfo: ActivatedRoute,
        public commfunc: Commfunc,
        public decimalPipe: DecimalPipe,
        public fb: FormBuilder,
        public confirmationService: ConfirmationService,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
    }

    ngOnInit() {
        this.codeValues();
        this.depositSumTypeOptions = this.code['depositSumTypeBigCus'];//存款累计方式
        this.userform = this.fb.group({
            'custNo': new FormControl('', Validators.required),
            'custName': new FormControl('', Validators.required),
        });
        this.moneyform = this.fb.group({
            'feeunitPrice': new FormControl('', Validators.required),
            'custId': new FormControl('', Validators.required),
            'custIdName': new FormControl('', Validators.required),
            'divideRate': new FormControl('')
        });
        this.detailList = this.routeInfo.snapshot.params;
        console.log(this.detailList)
        if (this.detailList.taskName == '机构专岗' || this.detailList.taskName == '总行机构专岗') {
            this.isShow = true;
        } else {
            this.isShow = false;
        }

        this.dataMap = JSON.parse(this.routeInfo.snapshot.params['dataMap']);
        this.flag = this.routeInfo.snapshot.params['markFlag'];
        this.depositBase = this.decimalPipe.transform(this.dataMap.depositBase, '1.2-2');
        this.loanBase = this.decimalPipe.transform(this.dataMap.loanBase, '1.2-2');
        this.bigCustNo = this.dataMap.bigCustNo;
        this.bigId = this.dataMap.bigId;
        //是否从历史进入
        if (this.flag == '1') {
            this.isHistory = false;
        } else {
            this.isHistory = true;
        }
        //当前为首节点，并且不是从历史进入
        if (this.routeInfo.snapshot.params['taskRate'] == '1' && this.flag != '2') {
            this.firstCodeFlag = true;
        }
        //大客户费用撤销审批
        if (this.detailList.littleClassNo == "116000") {
            this.firstCodeFlag = false;
            this.isHistory = true;
            this.isShow = false;
        }
        this.queryBase();//大客户基本信息
        this.queryCusList();//大客户名单信息
        this.divideInto();//大客户分成信息
        this.approveUpdate();//判断是调整还是申请
    }
    ngOnChanges() {

    }
    apply(num) {
        this.show = num;
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    // 修改业务数据
    docheck(i, param) {
        if (i == '1') {
            if (!this.depositBase) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '存款基期不能为空' });
                return
            }
        }
        if (i == '2') {
            if (!this.loanBase) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '贷款基期不能为空' });
                return
            }
        }
        this.dataMap.depositBase = this.commfunc.transform(this.depositBase).toString();
        this.dataMap.loanBase = this.commfunc.transform(this.loanBase).toString();
    }

    //基本信息查询
    queryBase() {
        let param = {
            bigCustNo: this.bigCustNo,
            isHis: '1',
            bigId: this.bigId
        }
        this.httpService.queryBigCustBaseInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.bigCusApproveBean.custNo = data.custNo;
                this.bigCusApproveBean.custName = data.custName;
                this.bigCusApproveBean.snrjckSum = data.snrjckSum;
                this.bigCusApproveBean.snrjdkjckSum = data.snrjdkjckSum;
                this.bigCusApproveBean.feeunitPrice = data.feeunitPrice;
                this.bigCusApproveBean.bigCustNo = data.bigCustNo;
                this.feeunitPrice = data.feeunitPrice;
                this.saveFeeMon = JSON.parse(JSON.stringify(data.feeunitPrice));
                //大客户费用撤销审批
                if (this.detailList.littleClassNo == "116000") {
                    this.depositBase = this.decimalPipe.transform(data.depositBase, "1.2-2");
                    this.loanBase = this.decimalPipe.transform(data.loanBase, "1.2-2");
                }
                //当前名单信息新增删除，数据变化之后重新刷新数据时,数据塞入dataMap传给工作流
                if (this.custListFlag == 1) {
                    this.depositBase = this.decimalPipe.transform(data.depositBase, "1.2-2");
                    this.loanBase = this.decimalPipe.transform(data.loanBase, "1.2-2");
                    this.dataMap.depositBase = this.commfunc.transform(data.depositBase).toString();
                    this.dataMap.loanBase = this.commfunc.transform(data.loanBase).toString();
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //查询大客户客户名单信息
    queryCusList() {
        let param = {
            bigCustNo: this.bigCustNo,
            pageNum: this.pageNumCusList,
            pageSize: this.pageSizeCusList,
            userName: this.userName,
            operationType: '0',
        }
        this.httpService.queryBigCustInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.data_cuslist = data.bigCustInfoNewList;
                this.totalCusList = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //客户名单信息翻页
    paginateCusList(event) {
        this.pageSizeCusList = event.rows * 1;
        this.pageNumCusList = event.page + 1;
        this.queryCusList();
    }
    //客户名单信息删除
    toDelete(data) {
        let temp = {
            bigCustId: data.bigId,
        }
        this.httpService.deleteBigCustInfo(temp).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '删除成功!' }];
                this.queryCusList();
                //当前名单信息新增删除，数据变化之后重新刷新数据
                this.custListFlag = 1;
                this.queryBase();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //大客户名单全部删除
    deleteAll() {
        if (this.data_cuslist.length == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'success', summary: '提示', detail: '当前表格没有数据,无法进行删除!' }];
            return
        }
        this.confirmationService.confirm({
            message: '是否确认全部删除?',
            accept: () => {
                let temp = {
                    bigCustNo: this.data_cuslist[0].bigCustNo,
                }
                this.httpService.deleteBatchBigCustInfo(temp).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '删除成功!' }];
                        this.queryCusList();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        });
    }
    //大客户分成信息
    divideInto() {
        let param = {
            bigCustNo: this.bigCustNo,
            pageNum: this.pageNumDivide,
            pageSize: this.pageSizeDivide,
            isHis: '1'
        }
        this.httpService.queryBigCustManager(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.bigCustManNewList && data.bigCustManNewList.length > 0) {
                    for (let item of data.bigCustManNewList) {
                        item.divideRate = JSON.parse(JSON.stringify(item.divideRate * 100)).toFixed(2);
                    }
                    this.dataTable = data.bigCustManNewList;
                    this.totalDivide = data.totalNum;
                    this.bigCusValue = this.dataTable;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //大客户分成信息翻页
    paginateDivide(event) {
        this.pageSizeDivide = event.rows * 1;
        this.pageNumDivide = event.page + 1;
        this.divideInto();
    }
    //新增客户信息
    add() {
        this.addFlagBtn = true;
        if (!this.custName || this.custName == '') {
            this.showName();
        }
        if (this.custNo != this.on_cust) {
            this.showName();
        }
        if (this.routeInfo.snapshot.params['this_Flag'] == '1') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '当前大客户信息不可以新增!' }];
            return
        }
        setTimeout(() => {
            //校验的
            for (const i in this.userform.controls) {
                this.userform.controls[i].markAsDirty();
            }
            //处理是否提交
            for (const i in this.userform.controls) {
                if (!this.userform.controls[i].valid) {
                    return;
                }
            }
            let params = {
                userName: this.userName,
                custNo: this.custNo,
                custName: this.custName,
                operationType: '0',
                bigCustNo: this.bigCustNo,
            }
            this.httpService.addBigCustInfo(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.addFlagBtn = false;
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '提示', detail: '新增成功!' }];
                    this.bigCustNo = data.bigCustNo;
                    if (data.bigId && data.bigId != '' && data.bigId != null) {
                        this.bigId = data.bigId;
                    }
                    this.queryCusList();
                    this.reset(1);
                    //当前名单信息新增删除，数据变化之后重新刷新数据
                    this.custListFlag = 1;
                    this.queryBase();
                } else {
                    this.addFlagBtn = false;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.addFlagBtn = false;
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }, 500);
    }
    //校验
    checkPerson() {
        if (this.custNo && this.custNo != '') {
            this.httpService.findUser({ custNo: this.custNo }).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.infoList != null) {
                        if (data.infoList.length > 0) {
                            this.manageId = data.infoList[0].manageId;
                            if (this.manageId == this.userId) {
                                this.showName();
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '提示', detail: '当前登陆人不是该客户的主联系人,请重新输入!' });
                            }
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: '没有可选的主联系人!' });
                        }
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '没有可选的主联系人!' });
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        }
    }
    //回显客户名称
    showName() {
        if (this.custNo && this.custNo != '') {
            let param = {
                custNo: this.custNo
            }
            this.httpService.selectCustBriefInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.on_cust = JSON.parse(JSON.stringify(this.custNo));
                    if (data.dataSourceId == 'BR00B' && this.user.targetId == '101011') {
                        this.custName = data.custName;
                    } else {
                        if (data.dataSourceId == 'BR00A' && this.user.targetId == '101010') {
                            this.custName = data.custName;
                        } else {
                            this.reset(1);
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '提示', detail: '当前非台行客户!' }];
                            return
                        }
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //校验客户号只能输入数字
    onKeyupNumber(event, num) {
        if (event.target.value != '') {
            if (checkNumber(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字!' }];
                return
            }
            if (num == 1) {
                if (custNoTen(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '客户号错误!' }];
                    return
                }
            } else if (num == 2) {
                if (event.target.value == '') {
                    this.custIdName = '';
                }
            }
        }
    }
    //保留两位小数
    checkNumtwo(event) {
        if (event.target.value != '') {
            if (decimalNumTwo(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '只能输入数字,输入格式请保留两位小数!' }];
                event.target.value = '';
                return
            }
        }
    }
    //校验工号
    checkNum() {
        if (this.custId && this.custId != '') {
            this.httpService.queryManagerInfo({ assiterId: this.custId }).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.userId && data.userId != '') {
                        this.queryUserId(data.userId);
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    queryUserId(item) {
        this.httpService.queryPersonRelListByPartyId({ partyIdList: [item] }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.on_userID = JSON.parse(JSON.stringify(this.custId));
                if (data.relList.length == 0 || data.relList == null) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "工号错误,请重新输入!" });
                    this.custId = '';
                    this.custIdName = '';
                    return;
                }
                if (data.relList[0].statusId != "0") {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "当前客户经理状态有误,请重新输入!" });
                    this.custId = '';
                    this.custIdName = '';
                    return;
                }
                this.custIdName = data.relList[0].lastName;
                this.partyId = data.relList[0].partyId;
                this.lastName = data.relList[0].lastName;
                this.relDepOrgList = data.relList[0].relDepOrgList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //重置
    reset(data) {
        if (data == 1) {
            this.custNo = '';
            this.custName = '';
        } else if (data == 2) {
            this.custId = '';
            this.custIdName = '';
            this.divideRate = '';
        }
    }
    //新增大客户费用信息
    addMoney() {
        this.addMoeyFlag = true;
        if (this.custId != this.on_userID) {
            this.checkNum();
        }
        //校验的
        for (const i in this.moneyform.controls) {
            this.moneyform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.moneyform.controls) {
            if (!this.moneyform.controls[i].valid) {
                return;
            }
        }
        if (Number(this.divideRate) == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '分成比例不得为0!' }];
            return
        }
        if (this.data_cuslist.length == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '名单信息不能为空!' }];
            return
        }
        if (this.dataTable.length == 5) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '最多录入5个客户经理!' }];
            return
        }
        if (Number(this.divideRate) > 100) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '分成比例不能大于100%!' }];
            return
        }
        if (this.dataTable.length >= 1) {
            let sum = Number(this.divideRate);
            for (let item of this.dataTable) {
                sum += Number(item.divideRate);
            }
            if (sum > 100) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '分成比例合计之和必须为100%!' }];
                return
            }
        }
        if (this.dataTable.length == 4) {
            let sum1 = Number(this.divideRate);
            for (let item of this.dataTable) {
                sum1 += Number(item.divideRate);
            }
            if (sum1 > 100) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '分成比例合计之和必须为100%!' }];
                return
            }
        }
        let itemParam;
        if (this.divideRate != '' && this.divideRate && this.divideRate != undefined) {
            itemParam = JSON.parse(JSON.stringify(this.divideRate));
        }
        let params = {
            bigCustNo: this.bigCustNo,
            relDepOrgList: this.relDepOrgList,
            orgId: this.orgId,
            orgName: this.orgName,
            feeunitPrice: this.feeunitPrice,
            divideRate: (itemParam / 100).toString(),
            assitUserId: this.custId,
            assitUserName: this.custIdName
        }
        this.httpService.addBigCustManager(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.addMoeyFlag = false;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '新增成功!' }];
                this.divideInto();
                this.reset(2);
                this.saveFeeMon = JSON.parse(JSON.stringify(this.feeunitPrice));
            } else {
                this.addMoeyFlag = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.addMoeyFlag = false;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //判断是调整还是申请
    approveUpdate() {
        let param = {
            bigCustNo: this.bigCustNo,
            pageSize: 100,
            pageNum: 1
        }
        this.httpService.queryBigCustHistory(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.bigCustHistRelList && data.bigCustHistRelList.length > 0) {
                    if (data.bigCustHistRelList[0].operationType == '0') {
                        this.stop_flag = true;
                    } else {
                        this.stop_flag = false;
                    }
                    if (this.detailList.littleClassNo == "116000") {
                        this.stop_flag = false;
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    toUpdate(data) {
        data.showUpdate = true;
    }
    //修改保存
    toUpdateSave(item) {
        let itemParam = JSON.parse(JSON.stringify(item))
        let param = {
            bigCustId: item.bigId,
            divideRate: (itemParam.divideRate / 100).toString(),
        }
        this.httpService.updateBigCustManager(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                item.showUpdate = false;
                this.divideInto();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //删除
    toDeleteMoney(data) {
        this.confirmationService.confirm({
            message: '是否确人删除?',
            accept: () => {
                let params = {
                    isAll: '1',
                    bigCustNo: data.bigCustNo,
                    bigCustId: data.bigId
                }
                this.httpService.deleteBigCustManager(params).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'success', summary: '提示', detail: '删除成功!' }];
                        this.divideInto();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }
        });
    }
    //再次修改费用单价
    changeAgain() {
        if (this.feeunitPrice != '' && this.feeunitPrice && (this.feeunitPrice != this.saveFeeMon)) {
            console.log('再次修改费用单价')
            let params = {
                bigCustNo: this.bigCustNo,
                relDepOrgList: [],
                orgId: '',
                orgName: '',
                feeunitPrice: this.feeunitPrice,
                divideRate: '',
                assitUserId: '',
                assitUserName: '',
                bigId: this.bigId,
            }
            this.httpService.addBigCustManager(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.dataMap['feeunitPrice'] = this.commfunc.transform(this.feeunitPrice).toString();
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
}
