import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Message } from 'primeng/primeng';
import { BigCusHttpService } from '../../http/big-cus.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from '../../../../../../@uisftech/common/service/http.constant';
import { DecimalPipe } from '@angular/common';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { decimalNumTwo, Equhundered, Equhundered2 } from '../../../../../../@uisftech/common/service/validator';

/**
 * 大客户利息/捐赠申请审批界面-洪满义
 */
@Component({
    selector: 'bigcustDonateApprove',
    templateUrl: './bigcustDonateApprove.component.html',
    styleUrls: ['./bigcustDonateApprove.component.scss'],
    providers: [BigCusHttpService, Commfunc, DecimalPipe],
})
export class BigCustomerDonateApproveComponent implements OnInit {
    openacctDate: string;
    ch: any = CALENDAR_CN;
    seqno: string;//大客户编号
    msgs: Message[] = [];
    resultList: any = {};
    PayType: any = [];//支付方式
    code: any;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    fileName: string;
    doDeal: any = [];
    createrName: string;//
    createdStamp: string;//
    dataApprove: any = [];
    dataList: any = {};
    Deptimelimit: any = [];
    UPLOAD: string = TZB_CUS_UPLOAD;//导入
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
    fileList: any = [];
    firstCodeFlag: boolean = false;//是否是首节点可以修改
    flag: string;
    //校验
    userform: FormGroup;
    openacctdate: Date = null;
    inValue: any;
    accountno: string;
    constructor(
        public activatedRoute: ActivatedRoute,
        public bigCusHttpService: BigCusHttpService,
        public commfunc: Commfunc,
        public decimalPipe: DecimalPipe,
        public fb: FormBuilder,
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.Deptimelimit = this.code['Deptimelimit'];//存款期限
        this.userform = this.fb.group({
            "accountno": new FormControl('', [Validators.pattern(/^[1-9]\d*$/)]),
            "openacctdate": new FormControl(''),
            "deptimelimit": new FormControl('', Validators.required),
            "depcash": new FormControl(''),
            "feeprice": new FormControl('', Validators.required),
            "getrate": new FormControl('', Validators.required),
            "addordonaterate": new FormControl('', Validators.required),
        });
    }


    ngOnInit() {
        this.seqno = this.activatedRoute.snapshot.params['seqno'];
        this.resultList = this.activatedRoute.snapshot.params;
        this.dataList = JSON.parse(this.resultList.dataMap);
        this.dataList.depcash = this.decimalPipe.transform(this.dataList.depcash, '1.2-2');//存款金额(万元)
        this.dataList.feeprice = this.decimalPipe.transform(this.dataList.feeprice, '1.2-2');//费用单价
        this.dataList.getrate = this.decimalPipe.transform(this.dataList.getrate, '1.4-4');//中标利率(%);
        this.accountno = this.dataList.accountno;//存款账号

        this.dataList.addordonaterate = this.decimalPipe.transform(this.dataList.addordonaterate, '1.4-4');//加价利率/捐赠利率(%)
        if (this.dataList.openacctdate) {
            this.dataList.openacctdate = new Date(this.dataList.openacctdate);//日期
            this.openacctDate = this.dataList.openacctdate;
        }
        if (this.dataList.fileList) {
            this.fileList = this.dataList.fileList;
            this.dataList.fileList = this.fileList;
        }
        this.flag = this.activatedRoute.snapshot.params['markFlag'];//判断我的历史中的追回
        if (this.activatedRoute.snapshot.params['taskRate'] == '1' && this.flag != '2') {//首节点是可以进行修改
            this.firstCodeFlag = true;
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    down(temp) {
        let name = temp.fileName;
        let path = temp.fileUrl;
        open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);
    }
    //上传文件
    myUploader(event) {
        if (event.xhr.response) {
            if (JSON.parse(event.xhr.response).success == false) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: JSON.parse(event.xhr.response).errorMsg });
                return
            } else {
                this.dataList.fileList = JSON.parse(event.xhr.response);
                this.fileList = this.dataList.fileList;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功！' });
            }
        }

    }
    onkey(event) {
        if (decimalNumTwo(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '输入正确的金额!' }];
            event.target.value = '';
            return
        }
    }
    textSum(num) {
        if (num == '1') {
            this.dataList.depcash = this.decimalPipe.transform(this.dataList.depcash, '1.2-2');//存款金额
        } else if (num == '2') {
            this.dataList.feeprice = this.decimalPipe.transform(this.dataList.feeprice, '1.2-2');//费用单价
        }
    }
    //校验百分比
    percenterBtn(event, num) {
        if (Equhundered2(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '输入正确的百分比!' }];
            event.target.value = '';
            return
        }
        if (num == '1') {
            this.dataList.getrate = this.decimalPipe.transform(this.dataList.getrate, '1.4-4');//中标利率
        } else if (num == '2') {
            this.dataList.addordonaterate = this.decimalPipe.transform(this.dataList.addordonaterate, '1.4-4');//加价利率
        }
    }
    //输入正确的存款账号时，开户日期自动回显
    showDate(item) {
        if (item && item != '') {
            if (item == this.accountno) {
                this.dataList.openacctdate = this.openacctDate;
            } else {
                let param = {
                    accountno: item
                }
                this.bigCusHttpService.getAccountInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.dataList.openacctdate = data.openacctdate;
                    } else {
                        this.dataList.accountno = '';
                        this.dataList.openacctdate = '';
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
                });
            }

        } else {
            this.dataList.openacctdate = '';
        }
    }

}
