import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BigCustomerComponent } from './bigCustomer.component';
import { BigCustomerDonateApproveComponent } from './big-cus-donate/bigcustDonateApprove/bigcustDonateApprove.component';
import { BigCustomerApproveComponent } from './big-cus-apply/bigCustomerApprove/bigCustomerApprove.component';

export const routes: Routes = [
    {
        path: '',
        component: BigCustomerComponent,
        children: [
            { path: '', redirectTo: 'big-cus-apply', pathMatch: 'full' },
            {
                path: 'big-cus-apply',
                loadChildren: "./big-cus-apply/big-cus-apply.module#BigCusApplyModule"
            },
            {
                path: 'big-cus-donate',
                loadChildren: "./big-cus-donate/big-cus-donate.module#BigCusDonateModule"
            },
        ]
    },
    {
        path:'bigcustDonateApprove',
        component:BigCustomerDonateApproveComponent
    },
    {
        path: 'bigCustomerApprove',
        component: BigCustomerApproveComponent
    }
]
export const BigCustomerRouting: ModuleWithProviders = RouterModule.forChild(routes);